/**
 * css 实现loading效果
 */
.dot{
	display: inline-block;
	height: 0.1rem;
	line-height: 0.1rem;
	overflow: hidden;
	text-align: left;
	letter-spacing: 0.03rem;
	margin-left: 0.02rem;
}
.dot::before{
	display: block;
	content: '...\A..\A.';
	white-space: pre-wrap;
	animation: dot 1.5s infinite step-start both;
	-webkit-animation: dot 1.5s infinite step-start both;
}
@keyframes dot{
33%{-webkit-transform: translateY(-0.2rem);transform: translateY(-0.2rem);
}
66%{-webkit-transform: translateY(-0.1rem);transform: translateY(-0.1rem);
}
}
